.cell {
    border: 1px solid black;
    width: 80px;
    height: 80px;
    display: inline-block;
}

.cell-chip {
    position: relative;
    line-height: 1em;
}

.board-list {
    list-style-type: none;
    padding: 0;
    gap: 4px;
    display: inline-grid;
    grid-template-columns: repeat(10, auto);
    flex-grow: 0;
}

.grid-item {
    line-height: 0;
}

.number-overlay {
    position: absolute;
    top: 0;
    left: 0;
}

button:disabled {
    opacity: 60%;
}

.effect-module {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.effect-module[data-isHighlighted=false] {
    opacity: 66%;
}

.effect {
    position: relative;
}
